<script>
	import { getContext } from 'svelte';

	const { width, height, xScale, yScale, yRange } = getContext('LayerCake');
	
	export let value;
	export let hasMarker = false;

	export let ticks = undefined;
	export let xTick = undefined;
	export let yTick = 16;
	export let dxTick = 0;

	export let layout;

	let unit = layout.unit ? layout.unit : '';
	let decimals = layout.decimals ? layout.decimals : 0;

	$: isBandwidth = typeof $xScale.bandwidth === 'function';

	$: tickVals = Array.isArray(ticks) ? ticks :
		isBandwidth ?
			$xScale.domain() :
			$xScale.ticks(ticks);

</script>

{#if hasMarker === true}
<g class='axis x-axis'>
		<g class='tick' transform='translate({$xScale(value)},{$yRange[0]})'>
			<line y1='{($height * -1) - 10}' y2='0' x1='0' x2='0'></line>
			<text
				x="{xTick || isBandwidth ? $xScale.bandwidth() / 2 : 0 }"
				y='{yTick}'
				dx='{dxTick}'
				dy='{($height * -1) - 30}'
				text-anchor='middle'>{value.toFixed(decimals)}{unit}</text>
		</g>
</g>
{/if}

<style>
	.tick {
		font-size: 0.9em;
		font-weight: 700;
	}

	line,
	.tick line {
		stroke: #000;
		stroke-width: 2;
	}

</style>