<svelte:options accessors={true} />

<script>
	import { LayerCake, Svg, flatten } from 'layercake';
	import { stack } from 'd3-shape';
	import { scaleBand, scaleOrdinal } from 'd3-scale';
	import { format, precisionFixed } from 'd3-format';
	import { tweened } from 'svelte/motion';
	import { cubicOut } from 'svelte/easing';

	import BarStacked from './BarStacked.svelte';
	import AxisX from './AxisX.svelte';
	import Marker from './Marker.svelte';
	import Share from '../share/App.svelte';

	export let breaks = [0, 100];
	export let value = null;
	export let layout = {};
	export let share = false;

	const anim = tweened(0, {
		duration: 400,
		easing: cubicOut
	});

	$: anim.set(value || breaks[0]);

	let data = makeData(breaks);
	
	function makeData(breaks) {
		let data = [{}];
		for (let i = 1; i < breaks.length; i ++) {
			let val = breaks[i] - breaks[i - 1];
			data[0]['q' + i] = val;
		}
		return data;
	}

	let xDomain = [breaks[0], breaks[breaks.length - 1]];
	let yDomain = [0];

	const xKey = [0, 1];
	const yKey = 'key';
	const zKey = 'key';

	const seriesNames = Object.keys(data[0]).filter(d => d !== yKey);
	let seriesColors = layout.colors ? layout.colors : ['rgb(210, 233, 254)', 'rgb(159, 201, 238)', 'rgb(24, 158, 212)', 'rgb(0, 97, 180)', 'rgb(0, 33, 104)'];

	data.forEach(d => {
		seriesNames.forEach(name => {
			d[name] = +d[name];
		});
	});

	const stackData = stack()
		.keys(seriesNames);

	const series = stackData(data);

	const formatTickX = d => format(`.${precisionFixed(d)}s`)(d);
</script>

<style>
	.chart-container {
		width: 100%;
		height: 50px;
	}
</style>

<div class="chart-container">
	<LayerCake padding={{ top: 20, bottom: 20, left: 5, right: 5 }} x={xKey} y={d=> d.data[yKey]}
		z={zKey}
		xDomain={xDomain}
		yScale={scaleBand().paddingInner([0]).round(true)}
		yDomain={yDomain}
		zScale={scaleOrdinal()}
		zDomain={seriesNames}
		zRange={seriesColors}
		flatData={flatten(series)}
		data={series}
		>
		<Svg>
			<BarStacked/>
			<AxisX snapTicks={false} formatTick={formatTickX} ticks={breaks} />
			<Marker value={$anim} hasMarker={value !=null} {layout} />
		</Svg>
	</LayerCake>
</div>
{#if share}
	<Share data={{breaks: breaks, value: value}} {layout} type="quantile"/>
{/if}