<script>
	export let data;
	export let layout;
	export let type = "";

	let code = false;
	let buttons;
	
	function embedCode(obj) {
		let svg = obj.previousElementSibling.firstChild.firstChild;
	  let width = svg.getBoundingClientRect().width;
    let height = svg.getBoundingClientRect().height;
		let iframe = ['<iframe width="' + width + '" height="' + height + '" src="', '" frameborder="0"></iframe>'];
		let chart = 'https://bothness.github.io/svelte-charts/embed/' + type + '.htm';
		let query = '?data=' + btoa(JSON.stringify(data)) + '&layout=' + btoa(JSON.stringify(layout));
		let code = iframe[0] + chart + query + iframe[1];
		return code;
	}
	
	function dwnSVG(obj) {
      //Get SVG string
      let string = obj.previousElementSibling.firstChild.firstChild.outerHTML;

      //Convert SVG string to BLOB
      let blob = new Blob([string], { type: "image/svg+xml;charset=utf-8" });
		
		  // Activate download
			let url = window.URL || window.webkitURL || window;
      let link = url.createObjectURL(blob);
		  download(link, 'chart.svg');
    }
	
	function dwnPNG(obj) {
		  let svg = obj.previousElementSibling.firstChild.firstChild;
	    let width = svg.getBoundingClientRect().width;
      let height = svg.getBoundingClientRect().height;
      var canvas = document.createElement('canvas');
      canvas.width = width;
      canvas.height = height;
      var data = new XMLSerializer().serializeToString(svg);
      var win = window.URL || window.webkitURL || window;
      var img = new Image();
      var blob = new Blob([data], { type: 'image/svg+xml' });
      var url = win.createObjectURL(blob);
      img.onloadend = function () {
        canvas.getContext('2d').drawImage(img, 0, 0);
        win.revokeObjectURL(url);
        var uri = canvas.toDataURL('image/png').replace('image/png', 'octet/stream');
        download(uri, 'image.png');
      }
      img.src = url;
    }
	
	function download(link, filename) {
      let a = document.createElement("a");
      a.download = filename;
      a.href = link;
      document.body.appendChild(a);
      a.click();
      document.body.removeChild(a);
	}
	
	function getData() {
		let string = 'label,value\n';
		for (let i in data) {
			string += data[i].label + ',' + data[i].value + '\n';
		}
		
		//Convert CSV string to BLOB
    let blob = new Blob([string], { type: "text/csv;charset=utf-8" });
		
		// Activate download
	  let url = window.URL || window.webkitURL || window;
    let link = url.createObjectURL(blob);
		download(link, 'data.csv');
	}
</script>

<style>
	.share-buttons {
		padding-top: 8px;
	}
	.embed {
		max-width: 400px;
		padding: 6px 10px;
		border: solid 1px #555;
		border-radius: 2px;
		overflow-wrap: anywhere;
		background-color: #fff;
		position: absolute;
    z-index: 10;
	}
	.share {
		font-size: 0.8em;
		padding: 2px 4px;
	}
</style>

<div class='share-buttons' bind:this={buttons}>
  <button class='share' on:click={() => {code = !code}}>Embed</button>
	<button class='share' on:click={() => dwnSVG(buttons)}>SVG</button>
	<button class='share' on:click={() => dwnPNG(buttons)}>PNG</button>
	<button class='share' on:click={getData}>CSV</button>
</div>
{#if code}
<div class='embed'>
	<code>{embedCode(buttons)}</code>
</div>
{/if}